* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

@media screen and (max-width: 1000px) {
  .super-alpha-wrapper {
    height: calc(100% + 25rem);
    width: 100%;
  }
  .alpha-wrapper {
    padding-top: 1rem;
    width: 100vw;
  }
  header {
    height: 12rem;
    width: 100%;
    justify-content: space-between;
  }
  header #header-img {
    width: 5rem;
    height: 5rem;
  }
  .work {
    margin-top: 2rem;
    padding: 1rem;
  }
  .work-grid {
    row-gap: 1rem;
    column-gap: 1rem;
    grid-template-rows: 1fr 1fr 1fr;
    grid-template-columns: 1fr 1fr;
    column-gap: 5rem;
    row-gap: 4rem;
    padding: 2rem;
    align-items: center;
    justify-content: center;
  }
  footer {
    margin-top: 0rem;
  }
}